import { Component } from '@angular/core';
import { Platform, IonicPage, ModalController, NavController, NavParams, ViewController, LoadingController, App } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { Storage } from '@ionic/storage';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';


@IonicPage()
@Component( {
  selector: 'page-pin-modal',
  templateUrl: 'pin-modal.html',
} )

export class PinModalPage {
  template: string = "<div></div>";
  body: any;
  data: any;
  completed: boolean = false;
  pageTo: string = "Login";
  token: any;
  ttype: string;
  pin: string = "";
  platform1: any= "";

  rate: number = 0;
  pinEntry: boolean;
  steps: number = 0;
  enteredKey: string;
  accounts: any = [];
  dataObject: any = {};
  type: string;

  one: string = "numberinput1";
  two: string = "numberinput1";
  three: string = "numberinput1";
  four: string = "numberinput1";

  encryptIsPresent = false;
  public unregisterBackButtonAction: any;
  objectblock: { username: string, password: string, lang: string,  platform1: string,version: string } = {
    username: '', password: '', lang: 'en',platform1: 'A', version: '0.03'
  };
  customer: any;
  langs: any = {};
  constructor( private storage: Storage, public modalCtrl: ModalController, public platform: Platform, private clientdata: ClientdataProvider, public appCtrl: App, public navParams: NavParams, public viewCtrl: ViewController, public alertService: AlertServiceProvider,
    public globalVars: GlobalVarsProvider, public loadingController: LoadingController, public navCtrl: NavController ) {
    this.objectblock.username = globalVars.Username;

    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
  }



  ionViewDidLoad() {
    console.log( 'ionViewDidLoad ConfirmModal' );
  }

  ionViewDidEnter() {
    this.initializeBackButtonCustomHandler();
  }

  ionViewWillLeave() {
    this.unregisterBackButtonAction && this.unregisterBackButtonAction();
  }

  public initializeBackButtonCustomHandler(): void {
    this.unregisterBackButtonAction = this.platform.registerBackButtonAction(() => {
      this.customHandleBackButton();
    }, 10 );
  }

  private customHandleBackButton(): void {
    this.exit();
  }

  submit() {
    if ( this.pin.length == 4 ) {
      this.viewCtrl.dismiss();
      this.login();
    }
  }
  exit() {
    // let message = this.langs.exiting;
    // let template = "<div>" + message + "</div>";
    // let obj = { body: "", template: template, ttype: "", completed: false, pageTo: '' };
    // let myModal = this.modalCtrl.create( 'ConfirmModalPage', obj );
    // myModal.present();
    // myModal.onDidDismiss( data => {
    //   console.log( "Data =>" + data );
    //   if ( data ) {
    //     this.platform.exitApp();
    //   }
    //   else {
    //   }
    // } );
    this.navCtrl.setRoot( 'LoginPage' );
  }

  public login() {
    if ( this.objectblock.username === null || this.objectblock.username === ''
      || this.objectblock.username.length < 9 ) {
      this.popmeth( this.langs.valid_topup_number );
    } else {
      this.objectblock.platform1 = 'A';
      this.objectblock.password = this.pin;
      this.objectblock.username = this.globalVars.trimPhome( this.objectblock.username );
      this.objectblock.password = this.globalVars.hashpin( this.objectblock.username + this.objectblock.password );
      this.alertService.showLoading( this.langs.submitting_txt );
      this.clientdata.login( this.objectblock )
        .subscribe( data => {
          this.alertService.dismiss();
          data = this.globalVars.testdec( data );
          console.log( data )
          if ( data.length != 0 ) {
            if ( !data.error ) {
              console.log( data );
              this.globalVars.trials = "0";
              let encrypted_phoneno_uuid = "" + this.globalVars.doAESencrypt( data.mwallet_account + "|" + this.globalVars.trials, "9393099339" );
              this.storage.set( 'pbz', encrypted_phoneno_uuid );

              var is_first_login = data.is_first_login;
              this.globalVars.Username = data.mwallet_account;
              this.globalVars.customer_details = data.customer_info;
              this.globalVars.linked_acc = data.linked_acc;
              this.globalVars.trials = data.trials;
              this.globalVars.partial = data.partial_reg;
              this.globalVars.is_first_login = data.is_first_login;
              this.globalVars.token = data.token;
              this.globalVars.wallet_balance = data.wallet_bal;
              this.objectblock.password = "";
              this.pin = "";
              if ( is_first_login ) {
                this.navCtrl.setRoot( 'ChangePinPage' );
              } else {
                this.navCtrl.setRoot( 'HomePage' );
              }
            } else if ( data.error ) {
              this.objectblock.password = "";
              this.pin = "";
              if ( data.field48.includes( 'Account not found' ) ) {
                this.popmeth( data.message );
                // this.navCtrl.setRoot( 'RegisterPage' );
              } else if ( data.field48.includes( 'Wrong password' ) ) {
                if ( this.globalVars.trials == "2" ) {
                  this.blockPin();
                } else {
                  this.globalVars.trials = parseInt( this.globalVars.trials ) + 1 + "";
                  let encrypted_phoneno_uuid = '' + this.globalVars.doAESencrypt( this.globalVars.Username + "|" + this.globalVars.trials, "9393099339" );
                  this.storage.set( 'pbz', encrypted_phoneno_uuid );
                  console.log( this.globalVars.trials );
                  this.popmeth( this.langs.wrong_credentials );
                  // this.navCtrl.setRoot( 'LoginPage' );

                  
                }
              } else {
                this.popmeth( data.message );
              }
            } else {
              this.popmeth( this.langs.tech );
              this.objectblock.password = "";
              this.pin = "";
            }
          } else {
            this.objectblock.password = "";
            this.pin = "";
            // this.alertService.dismiss();
            this.popmeth( this.langs.tech );
            
          }
        },
        error => {
          this.objectblock.password = "";
          this.pin = "";
          this.alertService.dismiss();
          console.log( error );
          this.popmeth( this.langs.internet );
        }
        );
    }
  }
  popmeth( message ) {
    let template = "<div>" + message + "</div>";
    let obj = { body: "", template: template, ttype: "", completed: true, pageTo: '' };
    let myModal = this.modalCtrl.create( 'ConfirmModalPage', obj );
    myModal.present();
    myModal.onDidDismiss( data => {
      // console.log( "Data =>" + data );
      // if ( data ) {
      //   this.navCtrl.setRoot( 'LoginPage' );
      // }
      // else {
      //   this.navCtrl.setRoot( 'LoginPage' );
      // }
      this.navCtrl.setRoot( 'LoginPage' );
    } );
  }

  change() {
    this.navCtrl.setRoot( 'RegisterPage' );
  }

  blockPin() {
    this.clientdata.block( this.objectblock )
      .subscribe( data => {
        data = this.globalVars.testdec( data );
        console.log( data )
        if ( data.length != 0 ) {
          if ( !data.error ) {
            this.popmeth( data.message );
            this.globalVars.trials = "0";
          } else if ( data.error ) {
            this.popmeth( this.langs.acc_blocked );
        
          } else {
            this.popmeth( this.langs.acc_blocked );
          
          }
        } else {
          this.popmeth( this.langs.tech );
     
        }
      }, err => {
        console.log( err );
        this.popmeth( this.langs.tech );
      
      } );
  }
}





